.lang_card{
    border-radius: 25px;
    margin: 0 0rem 3rem 0rem;
    box-shadow: 0 30px 30px rgba(0, 0, 0, 0.600);
    height: 6.5rem;
    color: rgba(255, 255, 255, 0.884);
    position: relative;
    text-align: center;
    cursor: pointer;
}
.lang_card::after{
    content: "";
    position: absolute;
    z-index: -1;
    bottom: 0%;
    left: 0%;
    height: 100%;
    width: 100%;
    opacity: 0.7;
    border-radius: 30px;
    
    /* Declaring our shadow color inherit from the parent (button) */
    background: inherit;
    
    /* Blurring the element for shadow effect */
    -webkit-filter: blur(12.5px);
    -moz-filter: blur(12.5px);
    -o-filter: blur(12.5px);
    -ms-filter: blur(12.5px);
    filter: blur(12.5px);
    
    /* Transition for the magic */
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}

.lang_logo{
    width: 4rem;
    position: absolute;
    margin: auto;
    top: -25px;
    left: 0; 
    right: 0;
    -webkit-filter: drop-shadow( 0 20px 20px rgba(0, 0, 0, 0.753));
    filter: drop-shadow( 0 20px 20px rgba(0, 0, 0, 0.753));    
}
.lang_logo_clicked{
    top: 25px;
    right: 120%;
    left: -20px;
    width: 3rem;
}
.lang_text{
    position: absolute;
    left: 0;
    margin: auto;
    right: 0;
    opacity: 0;
    width: 80%;
    top: 50%;
    transform: translateY(-50%);
    font-size: 0.9rem;
    line-height: 1.25;
    text-shadow:  0 2px 10px rgba(0, 0, 0, 0.7)
}
.lang_text_clicked{
    opacity: 0.8;
}
.lang_name{
    font-size: 1.4rem;
    padding-top: 3.5rem;
    opacity: 0.8;
}
.lang_name_clicked{
    opacity: 0;
}
.tool_card{
    border-radius: 20px;
    box-shadow: 0 30px 30px rgba(0, 0, 0, 0.600);
    height: 4rem;
    width: 80%;
    margin: 1.5rem auto;
    position: relative;
    text-align: center;
    transition: all 0.25s ease;
    cursor: pointer;
}

.tool_card:after{
    content: "";
    position: absolute;
    z-index: -1;
    bottom: 0%;
    left: 0%;
    height: 100%;
    width: 100%;
    opacity: 0.7;
    border-radius: 30px;
    
    /* Declaring our shadow color inherit from the parent (button) */
    background: inherit;
    
    /* Blurring the element for shadow effect */
    -webkit-filter: blur(12px);
    -moz-filter: blur(12px);
    -o-filter: blur(12px);
    -ms-filter: blur(12px);
    filter: blur(12px);
    
    /* Transition for the magic */
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}

  
.tool_logo{
    opacity: 1;
    width: 4rem;
    position: absolute;
    margin: auto;
    top: -30px;
    left: 0; 
    right: 0;
    
    -webkit-filter: drop-shadow( 0 20px 20px rgba(0, 0, 0, 0.753));
    filter: drop-shadow( 0 20px 20px rgba(0, 0, 0, 0.753));    
}
.tool_text {
    color:#ffffff00;
    position: absolute;
    margin: 0;
    left: 0; right: 0;
    font-weight: bold;
    top: 50%;
    transform: translateY(-50%);
    font-size: 75%;
}
.text_clicked{
    color: rgba(255, 255, 255, 0.700);
    text-shadow:  0 2px 10px rgba(0, 0, 0, 0.7)
}
.logo_clicked{
    opacity: 0;
}
