.tool_filter{
    border-radius: 10px;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.692);
    color: rgba(255, 255, 255, 0.763);
    background-color: rgba(0, 0, 0, 0.700);
    font-size: 80%;
    padding: 1rem;
    text-align: center;
    margin: 0 0.25rem 2rem;
    transition: all 0.25s ease;
}
.tool_filter:hover{
    box-shadow: 0 20px 20px black;
    color: rgba(255, 255, 255, 0.903);
    background-color: rgba(240, 240, 240, 0.25);
    transform: translateY(-5px);
    cursor: pointer;
}
.selected_filter{
    box-shadow: 0 20px 20px black;
    color: rgba(255, 255, 255, 0.903);
    background-color: rgba(103, 102, 102, 0.25);
    transform: translateY(-5px);
}

